import React, { useState } from "react";
import {
  Card,
  CardContent,
  Typography,
  TextField,
  Button,
  Stack,
} from "@mui/material";

export default function QueryPanel({ showSubgraph, showPath }) {
  const [node, setNode] = useState("");
  const [radius, setRadius] = useState(1);
  const [src, setSrc] = useState("");
  const [dst, setDst] = useState("");

  return (
    <Card elevation={4} sx={{ mb: 3 }}>
      <CardContent>
        <Typography variant="h5">Query Graph</Typography>

        <Stack spacing={2} mt={2}>
          <TextField
            label="Node"
            value={node}
            onChange={(e) => setNode(e.target.value)}
          />
          <TextField
            label="Radius"
            type="number"
            value={radius}
            onChange={(e) => setRadius(e.target.value)}
          />

          <Button
            variant="contained"
            onClick={() => showSubgraph(node, radius)}
          >
            Show Neighborhood
          </Button>

          <TextField
            label="Source Node"
            value={src}
            onChange={(e) => setSrc(e.target.value)}
          />
          <TextField
            label="Destination Node"
            value={dst}
            onChange={(e) => setDst(e.target.value)}
          />

          <Button variant="contained" onClick={() => showPath(src, dst)}>
            Find Shortest Path
          </Button>
        </Stack>
      </CardContent>
    </Card>
  );
}
