import React from "react";
import Graph from "react-vis-network-graph";

export default function GraphView({ graphData }) {
  const options = {
    layout: { improvedLayout: true },
    nodes: {
      shape: "dot",
      size: 14,
      color: "#1976d2",
      font: { color: "#000" },
    },
    edges: {
      arrows: "to",
      color: "#555",
      font: { align: "middle" },
    },
    physics: {
      enabled: true,
      stabilization: true,
      repulsion: { springLength: 120 },
    },
  };

  return (
    <div
      style={{
        height: "650px",
        background: "#fff",
        borderRadius: "8px",
        padding: "10px",
      }}
    >
      <Graph graph={graphData} options={options} />
    </div>
  );
}
